import React from "react"
import PropTypes from "prop-types"
import classnames from "classnames"
import { Link, graphql, useStaticQuery } from "gatsby"
import useMediaQuery from "@material-ui/core/useMediaQuery"
import Autocomplete from "@material-ui/lab/Autocomplete"
import { withStyles } from "@material-ui/core/styles"
import TextField from "@material-ui/core/TextField"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { Index } from "elasticlunr"
import get from 'lodash/get';

const TextSearch = withStyles(theme => ({
  root: {
    "& .MuiInput-underline:focus": {
      borderBottomColor: "transparent",
    },
    "& .MuiInput-underline:hover:before": {
      borderBottomColor: "transparent",
    },
    "& .MuiInput-underline:before": {
      borderBottomColor: "transparent",
    },
    "& .MuiInput-underline:after": {
      borderBottomColor: "transparent",
    },
    "& input": {
      color: "white",
      padding: "10px 0 0 0!important",
    },
  },
}))(TextField)
const AutocompleteSearch = withStyles(theme => ({
  root: {
    "&": {
      width: "150px",
      height: "40px",
    },
    "&.Mui-focused": {
      width: "250px",
    },
    "& .MuiInputBase-root": {
      marginTop: 0,
    },
    "& .MuiFormLabel-root": {
      top: "-11px",
      fontWeight: "bold",
      color: "white",
    },
    "& .MuiAutocomplete-popupIndicatorOpen": {
      transform: "rotate(0deg)",
    },
  },
}))(Autocomplete)

const firstOptions = [
  { name: "Threebond", path: "/" },
  { name: "Productos", path: "/productos" },
  { name: "Cotiza", path: "/cotiza" },
  { name: "Noticias", path: "/noticias" },
  { name: "Biblioteca", path: "/biblioteca" },
]

const navOrders = [
  'nosotros',
  'productos',
  'biblioteca',
  'cotiza',
  'contacto',
  'noticias'
];

const Header = (props) => {
  let { allStrapiPages, siteSearchIndex, file, usaFlag, japanFlag } = useStaticQuery(graphql`
    query Navigation {
      allStrapiPages {
        edges {
          node {
            name
          }
        }
      }
      siteSearchIndex {
        index
      }
      file(relativePath: { eq: "logo-neuro@2x.png" }) {
        childImageSharp {
          fluid {
            src
          }
        }
      }
      usaFlag: file(relativePath: { eq: "usa-flag.png" }) {
        childImageSharp {
          fluid {
            src
          }
        }
      }
      japanFlag: file(relativePath: { eq: "japan-flag.jpg" }) {
        childImageSharp {
          fluid {
            src
          }
        }
      }
    }
  `);
  let pathSlug = get(props, 'path', '/').split('/')[1];
  let [toggleMenuState, setToggleMenu] = React.useState(false)
  let [autocompleteSector, setAutocomplete] = React.useState(firstOptions)
  let indexRef = React.useRef(Index.load(siteSearchIndex.index))
  let searchRef = React.useRef()

  let handleAutocompleteChange = (event, value) => {
    window.location = value.path
  }
  let handleChange = event => {
    let query = event.target.value
    let results = indexRef.current
      .search(query, { expand: true })
      .map(({ ref }) => indexRef.current.documentStore.getDoc(ref))
    setAutocomplete(results || firstOptions)
  }

  let matches = useMediaQuery("(min-width:600px)")
  let matchesMobile = useMediaQuery("(max-width:599px)")

  let toggleMenu = () => setToggleMenu(!toggleMenuState)

  let navigationNodes = allStrapiPages.edges.filter(
              ({ node }) => node.name !== "product-views" && node.name !== "home")
    .concat([{ node: { name: 'contacto' } }])
    .sort((a, b) => navOrders.indexOf(a.node.name) - navOrders.indexOf(b.node.name));

  return (
    <header
      className={classnames("header", {
        desktop: matches,
        mobile: matchesMobile,
        show: toggleMenuState,
      })}
    >
      <div className="logo-container">
        {!matches ? (
          <button className="btn-ghost" onClick={toggleMenu}>
            <FontAwesomeIcon
              icon={["fas", "bars"]}
              style={{ color: "#ffffff" }}
            />
          </button>
        ) : null}
        <Link to="/">
          <img src={file.childImageSharp.fluid.src} alt="logo-threebond" />
          <span className="slogan">
            <h1>ThreeBond Manufacturing Mexicana</h1>
            <small>
              <i>Creating our future from a single drop</i>
            </small>
          </span>
        </Link>
        <span />
      </div>
      <nav className={classnames("nav-menu", { desktop: matches })}>
        <div>
          <a href="https://threebond.com" target="_blank"><img className="flag-img" src={usaFlag.childImageSharp.fluid.src} alt="usa-flag" /></a>
          <a href="https://www.threebond.co.jp/en/" target="_blank"><img className="flag-img" src={japanFlag.childImageSharp.fluid.src} alt="japan-flag" /></a>
        </div>
        <ul>
          {navigationNodes
            .map(({ node }) => {
              let pathName = `/${node.name}`
              return (
                <li className={node.name === pathSlug ? 'active': ''}>
                  <Link to={pathName}>{node.name}</Link>
                </li>
              )
            })}
            <li style={{ padding: 0 }}>
              <AutocompleteSearch
              id="combo-box-demo"
              options={autocompleteSector}
              getOptionLabel={option => option.name}
              onChange={handleAutocompleteChange}
              groupBy={option => option.group}
              popupIcon={
                <icon>
                  <FontAwesomeIcon
                    icon={["fas", "search"]}
                    style={{ color: "#ffffff", fontSize: "14px" }}
                  />
                </icon>
              }
              renderInput={params => {
                searchRef.current =
                  params.inputProps.ref && params.inputProps.ref.current
                return (
                  <TextSearch
                    {...params}
                    onChange={handleChange}
                    id="searchPage"
                    name="searchPage"
                    label="Buscar"
                  />
                )
              }}
            />
          </li>
        </ul>
      </nav>
    </header>
  )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
